.App {
  max-width: 700px;
}

.input {
  margin-top: 4px;
  border: 1px solid #eee;
  padding: 10px;
  display: flex;
}

.input-number {
  margin-right: 8px;
  font-weight: bold;
}

.input-features {
  margin-left: auto;
  text-align: right;
}

.input-key {
	font-size: 0.8rem;
	margin-top: 4px;
	color: #AAA;
}

.input-layers {
	margin-top: 2px;
	font-size: 0.8rem;
	color: #4F4F4F;
}

.input-layers .input-number {
	margin-right: 4px;
}

.input-layers-inputs {
	margin-top: 2px;
	margin-left: 6px;
}

.buttons {
	display: flex;
}

.buttons button {
  margin-right: 8px;
	margin-bottom: 8px;
}

.bus-checkbox {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.bus-checkbox > input[type="checkbox"] {
  margin: 0 2px;
}
